import { uploaderStyles } from "./style.js";

const blobToBase64 = (blob) => {
  const reader = new FileReader();
  reader.readAsDataURL(blob);
  return new Promise((resolve) => {
    reader.onloadend = () => {
      resolve(reader.result);
    };
  });
};

const ImageUploader = ({
  frameStyle,
  imageUrl,
  setImageUrl,
  previewWidth = 50,
  previewHeight = 50,
  editable = true,
}) => {
  return (
    <div style={{ ...uploaderStyles.frame, ...frameStyle }}>
      <div
        style={{
          ...uploaderStyles.fileSelector,
          width: previewWidth,
          height: previewHeight,
        }}
      >
        {imageUrl ? (
          <img
            alt={"no preview"}
            src={imageUrl}
            style={uploaderStyles.previewImage}
          />
        ) : (
          <div style={uploaderStyles.previewImage}></div>
        )}
        {editable && (
          <input
            style={{
              width: "100%",
              height: "100%",
              position: "absolute",
            }}
            onChange={async (e) => {
              if (e.target.files.length > 0)
                setImageUrl(await blobToBase64(e.target.files[0]));
            }}
            type="file"
            id="img"
            name="img"
            accept=".jpg, .png, .svg"
          />
        )}
      </div>
    </div>
  );
};
export default ImageUploader;
