import { successRes, errorRes } from "../../utils.js";

export const updateUserInfo = async (info, self) => {
  info = {
    ...info,
    userId: self.state.userId,
  };

  try {
    const response = await fetch(self.host + "/updateInfo", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(info),
      credentials: "include",
    });
    if (response.status !== 200) {
      console.log(
        "Looks like there was a problem. Status Code: " + response.status
      );
      return;
    }
    const res = await response.json();
    if (!res.error) {
      self.setState((state) => ({
        ...state,
        userInfo: {
          ...state.userInfo,
          ...info,
        },
      }));
      return successRes();
    } else {
      return errorRes(res.errMsg);
    }
  } catch (err) {
    return errorRes("Fetch Error", err);
  }
};
