import { successRes, errorRes } from "../../utils.js";
import FileSystem from "../../segments/FileSystem";

export const loadFileSystem = async (self) => {
  try {
    const response = await fetch(
      self.props.host + "/getFS/" + self.props.userId
    );
    if (response.status !== 200) {
      return errorRes(
        "Looks like there was a problem. Status Code: " + response.status
      );
    }
    const res = await response.json();
    self.setState((state) => ({
      ...state,
      fileSystem: new FileSystem(
        res.data,
        [self.setDataChanged],
        alert.bind(window)
      ),
    }));
    self.props.setLoading(false);
    return successRes(res);
  } catch (err) {
    return errorRes("Fetch Error", err);
  }
};

export const syncFileSystem = async (self) => {
  const fs = self.state.fileSystem;
  if (fs) {
    const data = fs.outputRawData();
    console.log(data);
    try {
      const response = await fetch(
        self.props.host + "/setFS/" + self.props.userId,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(data),
          credentials: "include",
        }
      );
      if (response.status !== 200) {
        return errorRes(
          "Looks like there was a problem. Status Code: " + response.status
        );
      }
      self.setState((state) => ({
        ...state,
        saved: true,
      }));
      const res = await response.text();
      return successRes(res);
    } catch (err) {
      return errorRes("Fetch Error", err);
    }
  }
};
