import React, { useState } from "react";
import AppIcon from "../../components/AppIcon";
import NotesApp from "./notes.jsx";
import { CarryOutOutlined } from "@ant-design/icons";
import { themeColors } from "../../config.js";

const Notes = ({ systemData, updateSystemData, scale }) => {
  const [isOpen, setOpen] = useState(false);
  return (
    <div>
      {isOpen && (
        <NotesApp
          setOpen={setOpen}
          systemData={systemData}
          updateSystemData={updateSystemData}
        />
      )}
      <AppIcon
        key={"notes"}
        scale={scale}
        icon={
          <CarryOutOutlined
            style={{ fontSize: 30, color: themeColors.yellow }}
          />
        }
        name={"Notes"}
        width={100}
        height={110}
        onClick={() => setOpen(!isOpen)}
        nameOnChange={false}
        nameChangable={false}
      />
    </div>
  );
};

export default Notes;
