import React, { useState } from "react";
import styles from "../../styles";
import AppIcon from "../../components/AppIcon";
import { DeleteOutlined } from "@ant-design/icons";
import WindowedFileExplorer from "../../components/WindowedFileExplorer";
import { themeColors } from "../../config.js";

const TrashBin = (props) => {
  const [open, setOpen] = useState(false);

  if (open) {
    return (
      <div>
        <WindowedFileExplorer
          fileSystem={props.fileSystem}
          id={"bin"}
          name={"Recycle Bin"}
          root={"bin"}
          closeWindow={() => setOpen(!open)}
          systemData={props.systemData}
        />
        <AppIcon
          key={"trash_bin"}
          icon={
            <DeleteOutlined
              style={{ fontSize: 30, color: themeColors.yellow }}
            />
          }
          name={"Recycle Bin"}
          width={100}
          height={110}
          onClick={() => setOpen(!open)}
          nameOnChange={false}
          nameChangable={false}
          scale={props.scale}
        />
      </div>
    );
  } else {
    return (
      <AppIcon
        key={"trash_bin"}
        icon={
          <DeleteOutlined style={{ fontSize: 30, color: themeColors.yellow }} />
        }
        name={"Recycle Bin"}
        width={100}
        height={110}
        onClick={() => setOpen(!open)}
        nameOnChange={false}
        nameChangable={false}
        scale={props.scale}
      />
    );
  }
};

export default TrashBin;
